* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Inter", sans-serif;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

html {
  font-size: 10px;
}

body {
  width: 100%;
  height: 100vh;
  font-size: 16px;
  overflow-x: hidden;
}

#root {
  width: 100%;
  height: 100%;
}

.icon {
  color: white !important;
  font-size: 5rem !important;
  cursor: pointer;
}
.icon--search {
  font-size: 2.5rem !important;
  color: #5F6973 !important;
}
.icon--card-option {
  font-size: 5rem !important;
  color: #b28f3d !important;
}
.icon--offers {
  font-size: 8rem !important;
  color: #373737 !important;
  margin: 0 auto;
  display: block !important;
  margin-bottom: 3rem !important;
}
.icon--close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  font-size: 3rem !important;
}
.icon--slider-back, .icon--slider-next {
  position: absolute !important;
  transition: 0.3s ease-in-out !important;
  padding: 1rem;
}
@media screen and (max-width: 430px) {
  .icon--slider-back, .icon--slider-next {
    width: 7rem;
    padding: 1.5rem;
  }
}
.icon--slider-back:hover, .icon--slider-next:hover {
  transform: scale(1.1) translateY(-50%) !important;
}
.icon--slider-back {
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  color: white !important;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}
.icon--slider-next {
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
  color: white !important;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}
.icon--drive {
  font-size: 2rem !important;
}
.icon--see {
  font-size: 4rem !important;
  transition: all 0.3s ease-out !important;
}
.icon--see:hover {
  transform: scale(1.1) !important;
}
.icon--contact {
  color: #1A3760 !important;
  font-size: 3rem !important;
}
@media screen and (max-width: 650px) {
  .icon--contact {
    font-size: 2rem !important;
  }
}

.car-card {
  padding: 1rem;
  background-color: white;
  border-radius: 1rem;
  border: 1px solid rgba(128, 128, 128, 0.3);
}
.car-card__img {
  width: 27rem;
  border-radius: 1rem;
  overflow: hidden;
  position: relative;
  margin-bottom: 2rem;
  cursor: pointer;
}
.car-card__img > img {
  width: 100%;
}
.car-card__img:hover .car-card__img__bg {
  height: 100%;
  opacity: 1;
}
.car-card__img__bg {
  width: 100%;
  height: 0;
  position: absolute;
  z-index: 100;
  bottom: 0;
  left: 0;
  transition: 0.4s ease-in-out;
  background-color: rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  opacity: 0;
  justify-content: center;
}
.car-card__price {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}
.car-card__title {
  font-weight: 200;
  margin-bottom: 1rem;
}
.car-card__stars {
  margin-bottom: 1rem;
}
.car-card > hr {
  background-color: red;
  margin-bottom: 1rem;
}
.car-card__info {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.car-card__info > span {
  display: flex;
  align-items: center;
}
.car-card__info > span > span {
  font-size: 1.2rem;
}

.car-option-card {
  width: 25rem;
  height: 30rem;
  position: relative;
  margin: 1rem;
  background: linear-gradient(rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.6)), url("https://autlines.tm-colors.info/wp-content/uploads/2019/08/bg-tabs-min.jpg");
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.car-option-card__content {
  width: 100%;
  height: 40%;
  background-color: #b28f3d;
  position: absolute;
  bottom: 0;
  -webkit-clip-path: polygon(27% 21%, 100% 0, 100% 100%, 0 100%, 0 0);
          clip-path: polygon(27% 21%, 100% 0, 100% 100%, 0 100%, 0 0);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.side-menu {
  position: fixed;
  top: 0;
  width: 70%;
  height: 100vh;
  background-color: #0A2357;
  z-index: 2000;
  padding-block: 5rem;
  transition: 0.4s ease-in-out;
}
.side-menu__logo {
  margin-left: 3rem;
}
.side-menu__logo > img {
  width: 12rem;
}
.side-menu__list {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.side-menu__list > * {
  opacity: 0;
}
.side-menu__list__item {
  color: white;
  margin: 4rem 1rem;
  transition: 0.3s ease-out;
  transform: translateX(-100px);
}
.side-menu__list__item:hover {
  transform: translateX(1rem);
}

.testDrivePopUp {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.testDrivePopUp__exit {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.testDrivePopUp__form {
  width: 70rem;
  background-color: #0A2357;
  border-radius: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  padding-block: 5rem;
  z-index: 100;
  position: relative;
}
@media screen and (max-width: 650px) {
  .testDrivePopUp__form {
    border-radius: 0.5rem;
    width: 65rem;
  }
}
.testDrivePopUp__form__title {
  font-size: 1.7rem;
  color: white;
  letter-spacing: 1px;
}
.testDrivePopUp__form__input {
  width: 25rem;
  height: 4rem;
  border-radius: 0.5rem;
  padding-left: 1rem;
  outline: none;
  border: none;
  transition: 0.3s ease-in-out;
}
.testDrivePopUp__form__input:focus {
  transform: translateY(-1rem) scale(1.1);
  box-shadow: 0 0 5px 1px #F5C34B;
}
.testDrivePopUp__form__textarea {
  width: 35rem;
  height: 10rem;
  border-radius: 0.5rem;
  padding: 1rem;
  outline: none;
  border: none;
  transition: 0.3s ease-in-out;
}
.testDrivePopUp__form__textarea:focus {
  transform: translateY(-1rem) scale(1.1);
  box-shadow: 0 0 5px 1px #F5C34B;
}
.testDrivePopUp__form__btn {
  width: 18rem;
  height: 4rem;
  cursor: pointer;
  background-color: #F5C34B;
  border-radius: 1rem;
  cursor: pointer;
  outline: none;
  border: none;
  transition: 0.3s ease-in-out;
}
.testDrivePopUp__form__btn:hover {
  background-color: #a57505;
  color: white;
  transform: scale(1.05);
}

.header-scrolled {
  background-color: white !important;
  padding: 3rem 15rem !important;
}
@media screen and (max-width: 860px) {
  .header-scrolled {
    padding: 4rem 2rem !important;
  }
}
.header-scrolled *, .header-scrolled li {
  color: #5F6973 !important;
}
.header-scrolled *:hover, .header-scrolled li:hover {
  color: #1947E7 !important;
}

.header {
  width: 100%;
  min-height: 10vh;
  background-color: transparent;
  display: flex;
  justify-content: space-between;
  padding: 4rem 15rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  transition: 0.5s ease-in-out;
}
@media screen and (max-width: 860px) {
  .header {
    padding: 4rem 1rem;
  }
}
.header__logo {
  flex: 2;
}
.header__list {
  display: flex;
  align-items: center;
  gap: 4rem;
  margin-right: 10rem;
}
@media screen and (max-width: 860px) {
  .header__list {
    margin-right: 3rem;
  }
}
@media screen and (max-width: 550px) {
  .header__list {
    display: none;
  }
}
.header__list__item {
  color: white;
  transition: 0.3s ease-in-out;
  font-weight: 200;
  cursor: pointer;
}
.header__list__item:hover {
  transform: translateY(-0.3rem);
}
.header__menu {
  cursor: pointer;
  display: none !important;
}
@media screen and (max-width: 550px) {
  .header__menu {
    display: block !important;
  }
}

.footer {
  background-color: #0A2357;
  min-height: 23vh;
  padding: 5rem;
}
.footer__logo {
  margin-bottom: 5rem;
}
.footer__boxes {
  display: flex;
  justify-content: center;
  gap: 1rem;
  width: 70%;
  margin: 0 auto;
  flex-wrap: wrap;
}
@media screen and (max-width: 550px) {
  .footer__boxes {
    gap: 3rem;
  }
}
.footer__box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 30rem;
}
@media screen and (max-width: 550px) {
  .footer__box {
    text-align: center;
  }
}
.footer__box > * {
  line-height: 2;
  color: white;
  font-size: 1.4rem;
  font-weight: 200;
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.footer__box > span:nth-child(1) {
  font-size: 1.8rem;
  font-weight: 500;
}

.section__one {
  position: relative;
  width: 100%;
  min-height: 110vh;
  background: url("http://demoapus1.com/voiture/wp-content/uploads/2021/12/slider1.jpg");
  background-size: cover;
}
@media screen and (max-width: 750px) {
  .section__one {
    min-height: 130vh;
  }
}
.section__one__center {
  position: absolute;
  left: 50%;
  top: 35%;
  transform: translateX(-50%);
  width: 75rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media screen and (max-width: 750px) {
  .section__one__center {
    width: 100%;
    top: 27%;
  }
}
.section__one__center > span:nth-child(1) {
  font-size: 6rem;
  font-weight: 500;
  color: white;
}
@media screen and (max-width: 550px) {
  .section__one__center > span:nth-child(1) {
    font-size: 5rem;
  }
}
@media screen and (max-width: 450px) {
  .section__one__center > span:nth-child(1) {
    font-size: 4rem;
  }
}
.section__one__center > span:nth-child(2) {
  color: white;
  font-size: 2rem;
  font-weight: 200;
  margin-bottom: 5rem;
}
.section__one__filter {
  background-color: white;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 4rem 4rem;
  border-radius: 1rem;
}
@media screen and (max-width: 750px) {
  .section__one__filter {
    flex-direction: column;
    align-items: center;
    gap: 4rem;
    padding: 4rem 4rem;
    transform: translateY(-2rem);
    width: 67%;
  }
}
@media screen and (max-width: 450px) {
  .section__one__filter {
    width: 98%;
  }
}
.section__one__filter__marke, .section__one__filter__model {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-start;
}
.section__one__filter__marke > select, .section__one__filter__model > select {
  width: 20rem;
  height: 5rem;
  border-radius: 0.8rem;
  border: 1px solid rgba(95, 105, 115, 0.3254901961);
  outline: none;
  cursor: pointer;
  padding-left: 1rem;
  color: rgba(95, 105, 115, 0.8352941176);
}
.section__one__filter__search-btn {
  display: flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-self: flex-end;
}
@media screen and (max-width: 750px) {
  .section__one__filter__search-btn {
    align-self: center;
  }
}
.section__one__filter__search-btn > div {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15rem;
  gap: 0.5rem;
  background-color: #f5c34b;
  color: #5f6973;
  height: 5rem;
  border-radius: 0.8rem;
  font-size: 1.5rem;
  cursor: pointer;
}

.section__two {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 7rem;
  gap: 2rem;
}
.section__two__title {
  font-size: 3rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 1.5rem 3rem;
  color: black;
  font-size: 4.5rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 6rem;
}
@media screen and (max-width: 710px) {
  .section__two__title {
    letter-spacing: 1px;
    padding: 1rem 2rem;
    font-size: 2rem;
  }
}
@media screen and (max-width: 450px) {
  .section__two__title {
    text-align: center;
    font-size: 2.5rem;
  }
}
.section__two__desc {
  width: 50%;
  text-align: center;
  line-height: 2;
  color: #5f6973;
}
@media screen and (max-width: 450px) {
  .section__two__desc {
    width: 70%;
  }
}

.section__three {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-block: 10rem;
  gap: 10rem;
  background-color: #F9F9F9;
}
@media screen and (max-width: 710px) {
  .section__three {
    gap: 6rem;
    overflow-y: scroll;
    justify-content: flex-start;
    padding-left: 2rem;
  }
}
.section__three > img:nth-child(2) {
  width: 15rem;
}
.section__three > img {
  width: 20rem;
  border: 2px solid rgba(95, 100, 105, 0.1803921569);
  padding: 2rem;
  transition: 0.3s ease-in-out;
  background-color: white;
  cursor: pointer;
  opacity: 1;
  border-radius: 4rem;
}
@media screen and (max-width: 1153px) {
  .section__three > img {
    width: 15rem;
  }
}
@media screen and (max-width: 710px) {
  .section__three > img {
    width: 13rem;
  }
}
.section__three > img:hover {
  transform: scale(1.1);
}

.section__four {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 10rem;
  margin-bottom: 5rem;
}
.section__four > * {
  opacity: 1;
}
.section__four__title {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 1.5rem 3rem;
  color: black;
  font-size: 4.5rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 4rem;
}
@media screen and (max-width: 710px) {
  .section__four__title {
    letter-spacing: 1px;
    padding: 1rem 2rem;
    font-size: 2rem;
  }
}
.section__four__filter {
  display: flex;
  background-color: #EEF2F6;
  padding: 1rem;
  gap: 1rem;
  border-radius: 3.5rem;
  box-shadow: 0px 0px 10px 1px rgba(79, 79, 79, 0.2);
  margin-bottom: 5rem;
}
.section__four__filter > div {
  padding: 1rem 3rem;
  border-radius: 3.5rem;
  cursor: pointer;
  color: #5f6973;
}
@media screen and (max-width: 750px) {
  .section__four__filter > div {
    padding: 0.8rem 1rem;
    font-size: 1.5rem;
  }
}
.section__four__filter > div:hover {
  color: #1947E7;
}
.section__four__filter > .active {
  background-color: white;
}
.section__four__cars {
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
  justify-content: center;
  width: 90%;
  margin: 0 auto;
}

.section__five {
  display: flex;
  width: 80%;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 15rem;
}
.section__five__left {
  flex: 5;
  margin-right: 7rem;
}
@media screen and (max-width: 710px) {
  .section__five__left {
    margin: 0;
    text-align: center;
  }
}
.section__five__left > h1 {
  font-size: 4rem;
  text-align: center;
  width: 60%;
  margin: 0 auto;
  margin-bottom: 4rem;
  letter-spacing: 2px;
}
@media screen and (max-width: 550px) {
  .section__five__left > h1 {
    width: 100%;
    font-size: 3rem;
  }
}
.section__five__left div {
  font-weight: 300;
  line-height: 2;
  color: rgb(84, 83, 83);
  margin-bottom: 3rem;
}
.section__five__left > h2 {
  color: #B28F3D;
  font-size: 2.6rem;
  margin-bottom: 3rem;
}
.section__five__left__contact {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.section__five__right {
  flex: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
}
.section__five__right__wrapper {
  display: flex;
}
@media screen and (max-width: 710px) {
  .section__five__right__wrapper {
    display: block;
  }
}

.section__sex {
  width: 100%;
  background: linear-gradient(to bottom, rgba(31, 31, 31, 0.8941176471) 60%, white 60%);
  padding-top: 10rem;
  margin-bottom: 15rem;
  scale: 1;
}
@media screen and (max-width: 710px) {
  .section__sex {
    background: linear-gradient(to bottom, rgba(31, 31, 31, 0.8941176471) 80%, white 60%);
  }
}
.section__sex__title {
  color: white;
  font-size: 5rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 10rem;
}
@media screen and (max-width: 710px) {
  .section__sex__title {
    font-size: 3rem;
  }
}
.section__sex__offers {
  display: flex;
  gap: 3rem;
  flex-wrap: wrap;
  justify-content: center;
}
@media screen and (max-width: 710px) {
  .section__sex__offers {
    gap: 2rem;
  }
}
.section__sex__offers__item:nth-child(2) {
  transform: translateY(-4rem);
}
.section__sex__offers__item {
  height: 35rem;
  background-color: white;
  width: 40rem;
  position: relative;
  padding-top: 3rem;
}
@media screen and (max-width: 710px) {
  .section__sex__offers__item {
    margin: 2rem;
  }
}
.section__sex__offers__item__title {
  color: #B28F3D;
  text-align: center;
}
.section__sex__offers__item__content {
  width: 100%;
  height: 50%;
  background-color: #B28F3D;
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-clip-path: polygon(26% 16%, 100% 0, 100% 100%, 0 100%, 0 0);
          clip-path: polygon(26% 16%, 100% 0, 100% 100%, 0 100%, 0 0);
}
.section__sex__offers__item__content:hover .section__sex__offers__item__content__bg {
  opacity: 1;
  background-size: cover;
}
@media screen and (max-width: 550px) {
  .section__sex__offers__item__content {
    font-size: 1.3rem;
  }
}
.section__sex__offers__item__content > span {
  color: white;
  line-height: 2;
  width: 80%;
  text-align: center;
}
.section__sex__offers__item__content__bg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: 0.5s ease-in-out;
}

.section__seven {
  margin-bottom: 10rem;
  background-color: #EEF2F6;
  padding-block: 5rem;
}
.section__seven__title {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  padding: 1.5rem 3rem;
  color: black;
  font-size: 4.5rem;
  letter-spacing: 1px;
  text-transform: capitalize;
  text-align: center;
  margin-bottom: 10rem;
}
@media screen and (max-width: 710px) {
  .section__seven__title {
    letter-spacing: 1px;
    padding: 1rem 2rem;
    font-size: 2rem;
  }
}
.section__seven img {
  width: 20rem;
  padding: 1.5rem 2.5rem;
  border: 1px solid rgba(128, 128, 128, 0.3);
  transition: 0.4s ease-in-out;
  cursor: pointer;
}
@media screen and (max-width: 910px) {
  .section__seven img {
    width: 15rem;
  }
}
@media screen and (max-width: 550px) {
  .section__seven img {
    width: 10rem;
  }
}
@media screen and (max-width: 350px) {
  .section__seven img {
    width: 6rem;
    padding: 0.5em 1rem;
  }
}
.section__seven img:hover {
  transform: scale(1.1);
  box-shadow: 0 0 10px 5px rgba(128, 128, 128, 0.1);
}
.section__seven__top {
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
  gap: 10rem;
  margin-bottom: 5rem;
}
@media screen and (max-width: 1153px) {
  .section__seven__top {
    gap: 5rem;
  }
}
@media screen and (max-width: 710px) {
  .section__seven__top {
    gap: 2rem;
  }
}
@media screen and (max-width: 500px) {
  .section__seven__top {
    gap: 1rem;
  }
}
.section__seven__bottom {
  margin: 0 auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  display: flex;
