.section {
}

.section__one {
  position: relative;
  width: 100%;
  min-height: 110vh;
  background: url("http://demoapus1.com/voiture/wp-content/uploads/2021/12/slider1.jpg");
  background-size: cover;
  @media screen and (max-width:750px) {
    min-height: 130vh;
  }
  &__center {
    position: absolute;
    left: 50%;
    top: 35%;
    transform: translateX(-50%);
    width: 75rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    align-items: center;
    @media screen and (max-width:750px) {
      width:100%;
      top:27%;
    }
    & > span:nth-child(1) {
      font-size: 6rem;
      font-weight: 500;
      color: white;
      @media  screen and (max-width:550px) {
        font-size: 5rem;
      }

      @media  screen and (max-width:450px) {
        font-size: 4rem;
      }
    }
    & > span:nth-child(2) {
      color: white;
      font-size: 2rem;
      font-weight: 200;
      margin-bottom: 5rem;
    }
  }

  &__filter {
    background-color: white;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 4rem 4rem;
    border-radius: 1rem;
    @media screen and (max-width:750px) {
      flex-direction: column;
      align-items: center;
      gap:4rem;
      padding: 4rem 4rem;
      transform: translateY(-2rem);
      width:67%;
    }
    @media  screen and (max-width:450px) {
      width:98%;
    }

    &__marke,
    &__model {
      display: flex;
      flex-direction: column;
      gap: 1rem;
      align-items: flex-start;

      & > select {
        width: 20rem;
        height: 5rem;
        border-radius: 0.8rem;
        border: 1px solid #5f697353;
        outline: none;
        cursor: pointer;
        padding-left: 1rem;
        color: #5f6973d5;
      }
    }
    &__search-btn {
      display: flex;
      height: fit-content;
      align-self: flex-end;
      @media screen and (max-width:750px) {
        align-self: center;
      }
      & > div {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 15rem;
        gap: .5rem;
        background-color: #f5c34b;
        color: #5f6973;
        height: 5rem;
        border-radius: 0.8rem;
        font-size: 1.5rem;
        cursor: pointer;
      }
    }
  }
}

.section__two {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-block: 7rem;
  gap: 2rem;

  &__title {
    font-size: 3rem;
    width:fit-content;
    margin:0 auto;
    padding:1.5rem 3rem;
    color:black;
    font-size: 4.5rem;
    letter-spacing: 1px;
    text-transform: capitalize;
    text-align: center;
    margin-bottom: 6rem;
    @media  screen and (max-width:710px) {
      letter-spacing: 1px;
      padding:1rem 2rem;
      font-size: 2rem;
    }
    @media  screen and (max-width:450px) {
      text-align: center;
      font-size: 2.5rem;
    }
  }

  &__desc {
    width: 50%;
    text-align: center;
    line-height: 2;
    color: #5f6973;
    @media  screen and (max-width:450px) {
      width:70%;
    }
  }
}


.section__three{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-block:10rem;
    gap:10rem;
    background-color: #F9F9F9;
    @media  screen and (max-width:710px) {
      gap:6rem;
      overflow-y: scroll;
      justify-content: flex-start;
      padding-left:2rem;
    }
    &>img:nth-child(2){
      width:15rem;
    }
    &>img{
        width:20rem;
        border:2px solid #5f64692e;
        padding:2rem;
        transition: .3s ease-in-out;
        background-color: white;
        cursor: pointer;
        opacity:1;
        border-radius: 4rem;
        @media  screen and (max-width:1153px) {
          width:15rem;
        }
        @media  screen and (max-width:710px) {
          width:13rem;
        }
        &:hover{
            transform: scale(1.1);
        }
    }
}

.section__four{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-block: 10rem;
    margin-bottom:5rem;
    &>*{
      opacity: 1;
    }
    &__title{
        width:fit-content;
        margin:0 auto;
        padding:1.5rem 3rem;
        color:black;
        font-size: 4.5rem;
        letter-spacing: 1px;
        text-transform: capitalize;
        text-align: center;
        @media  screen and (max-width:710px) {
          letter-spacing: 1px;
          padding:1rem 2rem;
          font-size: 2rem;
        }
        margin-bottom:4rem;
    }

    &__filter{
      display: flex;
      background-color: #EEF2F6;
      padding:1rem;
      gap:1rem;
      border-radius: 3.5rem;
      box-shadow: 0px 0px 10px 1px rgba(79, 79, 79, 0.2);
      margin-bottom:5rem;
      &>div{
        padding:1rem 3rem;
        border-radius: 3.5rem;
        @media screen and (max-width:750px) {
          padding:0.8rem 1rem ;
          font-size:1.5rem;
        }
        cursor: pointer;
        color: #5f6973;

        &:hover{
          color:#1947E7
        }
      }
      
      &> .active{
        background-color: white;
      }

    }

    &__cars{
       display: flex;
       gap:3rem;
       flex-wrap: wrap;
       justify-content: center;
       width:90%;
       margin:0 auto;
    }
}


.section__five{
  display: flex;
  width:80%;
  margin:0 auto;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom:15rem;
  
  &__left{
    flex:5;
    margin-right:7rem;
    @media  screen and (max-width:710px) {
      margin:0;
      text-align: center;
    }
    
    &>h1{
      font-size: 4rem;
      text-align: center;
      width:60%;
      margin: 0 auto;
      margin-bottom: 4rem;
      letter-spacing: 2px;
      @media  screen and (max-width:550px) {
        width:100%;
        font-size: 3rem;
      }
    }

    & div{
      font-weight: 300;
      line-height: 2;
      color:rgb(84, 83, 83);
      margin-bottom: 3rem;
    }

    &>h2{
      color:#B28F3D;
      font-size: 2.6rem;
      margin-bottom: 3rem;
    }

    &__contact{
      display: flex;
      align-items: center;
      gap:2rem;
    }
  }


  &__right{
    flex:2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    flex-direction: column;

    &__wrapper{
      display: flex;
      @media  screen and (max-width:710px) {
        display: block;
      }
    }

  }
}

.section__sex{
  width:100%;
  background: linear-gradient(to bottom,#1f1f1fe4 60%, white 60%);
  padding-top: 10rem;
  margin-bottom:15rem;
  scale: 1;
  @media  screen and (max-width:710px) {
    background: linear-gradient(to bottom,#1f1f1fe4 80%, white 60%);

  }
  &__title{
    color:white;
    font-size: 5rem;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 10rem;
    @media  screen and (max-width:710px) {
      font-size:3rem;
    }
  }

  &__offers{
    display: flex;
    gap:3rem;
    flex-wrap: wrap;
    justify-content: center;
    @media  screen and (max-width:710px) {
      gap:2rem;
    }

    &__item:nth-child(2){
      transform: translateY(-4rem);
    }
    &__item{
      height: 35rem;
      background-color: white;
      width:40rem;
      position:relative;
      padding-top:3rem;
      @media  screen and (max-width:710px) {
        margin:2rem;
      }
      &__title{
        color:#B28F3D;
        text-align: center;
      }


      &__content{
        width:100%;
        height:50%;
        background-color: #B28F3D;
        position:absolute;
        bottom:0;
        display: flex;
        align-items: center;
        justify-content: center;
        clip-path: polygon(26% 16%, 100% 0, 100% 100%, 0 100%, 0 0);
        &:hover &__bg{
          opacity: 1;
          background-size: cover;
        }
        @media  screen and (max-width:550px) {
          font-size: 1.3rem;
        }

        &>span{
          color:white;
          line-height: 2;
          width:80%;
          text-align: center;
        };

        &__bg{
          width:100%;
          height:100%;
          position: absolute;
          top:0;
          left:0;
          opacity: 0;
          transition:.5s ease-in-out;
        }
      }

    }
  }
}


.section__seven{
  margin-bottom:10rem;
  background-color: #EEF2F6;
  padding-block: 5rem;
  &__title{
    width:fit-content;
    margin:0 auto;
    padding:1.5rem 3rem;
    color:black;
    font-size: 4.5rem;
    letter-spacing: 1px;
    text-transform: capitalize;
    text-align: center;
    margin-bottom: 10rem;
    @media  screen and (max-width:710px) {
      letter-spacing: 1px;
      padding:1rem 2rem;
      font-size: 2rem;
    }
  }
  & img{
    width:20rem;
    padding:1.5rem 2.5rem;
    border:1px solid rgba(128, 128, 128, 0.3);
    transition:.4s ease-in-out;
    cursor: pointer;
    @media  screen and (max-width:910px) {
      width:15rem;
    }
    @media  screen and (max-width:550px) {
      width:10rem;
    }
    @media  screen and (max-width:350px) {
      width:6rem;
      padding:.5em 1rem;
    }
    &:hover{
      transform: scale(1.1);
      box-shadow: 0 0 10px 5px rgba(128, 128, 128, 0.1);
    }
  }
  &__top{
    margin:0 auto;
    width:fit-content;
    display: flex;
    gap:10rem;
    margin-bottom:5rem;
    @media  screen and (max-width:1153px) {
      gap:5rem;
    }
    @media  screen and (max-width:710px) {
      gap:2rem;
    }
    @media  screen and (max-width:500px) {
      gap:1rem;
    }
  }
  &__bottom{
    margin:0 auto;
    width:fit-content;
    display: flex;
    gap:20rem;
    @media  screen and (max-width:1153px) {
      gap:10rem;
    }
    @media  screen and (max-width:710px) {
      gap:4rem;
    }
    @media  screen and (max-width:500px) {
      gap:1.5rem;
    }
    @media  screen and (max-width:350px) {
      gap:.5rem;
    }

  }

}