.footer{
    background-color: #0A2357;
    min-height:23vh;
    padding: 5rem;

    &__logo{
        margin-bottom: 5rem;
    }
    &__boxes{
        display: flex;
        justify-content: center;
        gap:1rem;
        width:70%;
        margin:0 auto;
        flex-wrap: wrap;
        @media  screen and (max-width:550px) {
            gap:3rem;
          }
    }
    &__box{
        display: flex;
        flex-direction: column;
        align-items: center;
        width:30rem;
        @media  screen and (max-width:550px) {
            text-align: center;
          }
        &>*{
            line-height: 2;
            color:white;
            font-size:1.4rem;
            font-weight: 200;
            margin:0 auto;
            width: fit-content;

        }
        &>span:nth-child(1){
            font-size:1.8rem;
            font-weight: 500;
        }
    }
}