.contact{
    &__title{
        justify-content: flex-start;
        gap:2rem;
        align-items: center;
        margin-bottom: 10rem;
    }
    &__flex{
        @media screen and (max-width:650px) {
            flex-direction: column;
        }
    }

    &__left{
        flex:2;
        @media screen and (max-width:650px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__title{
            font-size: 2rem;
            margin-bottom:3rem;
            color:#1A3760;
        }
        &__desc{
            width:35%;
            letter-spacing: 1px;
            line-height: 1.3;
            margin-bottom: 2rem;
            font-weight: 200;
            @media screen and (max-width:650px) {
                width:60%;
            }
        }

        &__tlf{
            color:#1A3760;
            font-size: 1.9rem;
            margin-bottom: 2rem;
        }
        &__email{
            font-weight: 200;
        }
    }


    &__right{
        flex:3;
        align-items: flex-start;
        padding:0;

        &__title{
            font-size: 2rem;
            margin-bottom:3rem;
            color:#1A3760;
        }
        &__form{
            display: flex;
            flex-direction: column;
            gap:3rem;
            width:50rem;
            @media screen and (max-width:650px) {
                width:100%;
            }

            &__input-wrapper{
                display: flex;
                gap:1rem;
                @media screen and (max-width:500px) {
                    flex-direction: column;
                }
                    &>input{
                        border:1px solid gray;
                        outline: none;
                        width:25rem;
                        height:4rem;
                        border-radius: 1rem;
                        padding-left:1rem;
                        font-weight: 200;
                        @media screen and (max-width:650px) {
                            width:15rem;
                            height:3rem;
                            border-radius: .3rem;
                        }
                        @media screen and (max-width:500px) {
                            width:70%;
                        }
                    }

            }
            &>textarea{
                padding-left:1rem;
                border:1px solid gray;
                outline: none;
                height:15rem;
                border-radius: 1rem;
                padding-top:1rem;
                font-weight: 200;
                @media screen and (max-width:650px) {
                    width:100%;
                    border-radius: .4rem;
                }
            }

            &__btn{
                background-color: #F5C34B;
                width:20rem;
                height:4rem;
                border:none;
                cursor: pointer;
                border-radius: 1rem;
                color:#1A3760;
                transition:.3s ease-in-out;
                @media screen and (max-width:650px) {
                    align-self: center;
                }
                &:hover{
                    background-color: #d29b1a;
                    color:white;
                }
            }

            
        }
    }
}