.car-card{
    padding:1rem;
    background-color: white;
    border-radius: 1rem;
    border:1px solid rgba(128, 128, 128, 0.3);
    &__img{
        width:27rem;
        border-radius: 1rem;
        overflow: hidden;
        position:relative;
        margin-bottom:2rem;
        cursor: pointer;

        &>img{
            width:100%;
        }
        &:hover &__bg{
            height:100%;
            opacity:1;
        }
        &__bg{
            width:100%;
            height:0;
            position: absolute;
            z-index: 100;
            bottom:0;
            left:0;
            transition:.4s ease-in-out;
            background-color: rgba(0, 0, 0, 0.2);
            display: flex;
            align-items: center;
            opacity:0;
            justify-content: center;
        }

    }

    &__price{
        margin-bottom:1rem;
        font-size: 1.2rem;
    }
    &__title{
        font-weight: 200;
        margin-bottom:1rem;
    }
    &__stars{
        margin-bottom: 1rem;
    }

    &>hr{
        background-color: red;
        margin-bottom: 1rem;
    }

    &__info{
        display: flex;
        align-items: center;
        gap:2rem;
        &>span{
            display: flex;
            align-items: center;
            &>span{
                font-size: 1.2rem;
            }
        }
    }
}