.test-drive__form{
    display: flex;
    flex-direction: column;

    &>select,&>input{
        width:30rem;
        height:3.5rem;
        outline: none;
        margin-block: 2rem;
        transition:.4s ease-in-out;
        padding-left:1rem;
        cursor: pointer;
        &:hover{
            transform: scale(1.05);
        }
    }

    &>span{
        font-size:2rem;
        font-weight: lighter;
        color:#b37e02;
    };

    &__btn{
        width:25rem;
        height:4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        gap:1rem;
        border-radius: 1rem;
        transition:.3s ease-in-out;
        cursor:pointer;
        background-color: #F5C34B;
        color:#1A3760;

        &:hover{
            background-color: #d29b1a;
            color:white;
        }
    }
}


.calendar{
    width:60rem;
    display: flex;
    @media screen and (max-width:660px) {
        width:100%;
    }


    &__left{
        flex:1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        @media screen and (max-width:990px) {
            align-items: center;
        }
    }
    &__right{
        flex:3;
        display: flex;
        align-items: center;
        justify-content: center;
        gap:0;

        &__title{
            font-size:3rem;
            color:#b37e02;
            margin-bottom:5rem;
        }
    }


    &__one{
        &>.inner{
            width:14rem;
            height:4rem;
            border-left: 1px solid #b37e02;
            color:red;
            font-weight: 200;
            @media screen and (max-width:660px) {
                height:2.5rem;
            }

        }
        &>.inner:last-child{
            border-right: 1px solid #b37e02;
        }
    }
    &__two{
        &>.inner{
            width:14rem;
            height:8rem;
            border-top:1px solid #b37e02;
            border-left:1px solid #b37e02;
            @media screen and (max-width:660px) {
                height:5rem;
            }
        }
    }
    &__two{
        &>.inner:last-child{
            border-right:1px solid #b37e02;
        }
    }

    .inner{
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: .3s ease-in-out;
        background-color:#f5c24b3a;
        @media screen and (max-width:660px) {
            width:7rem !important;
        }

        &:hover{
            transform: scale(1.1);
            background-color: red;
            color:white;
        }
    }

    .inner.active{
        background-color: green;
        color:white;
        border:none;
    }
}