.car-option-card{
    width:25rem;
    height:30rem;
    position:relative;
    margin:1rem;
    background: linear-gradient(rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.6)),url("https://autlines.tm-colors.info/wp-content/uploads/2019/08/bg-tabs-min.jpg");
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;

    &__content{
        width:100%;
        height:40%;
        background-color: #b28f3d;
        position:absolute;
        bottom:0;
        clip-path: polygon(27% 21%, 100% 0, 100% 100%, 0 100%, 0 0);
        display: flex;
        align-items: center;
        justify-content: center;
        color:white;

    }
}