.single-car-page{
    min-height: 100vh;
    width: 100%;
    background-color: #F9F9F9;
    padding-top:25vh;
    padding-bottom:18vh;
    padding-left:10rem;
    @media screen and (max-width:1200px) {
        padding-inline:5rem;
    }
    @media screen and (max-width:430px) {
        padding-inline:2rem;;
    }

    &__title{
        font-size: 2rem;
        margin-bottom: 5rem;
        color:#1A3760;
        display: flex;
        justify-content: space-between;
        padding-right:10rem;
        @media screen and (max-width:1200px) {
            padding-right:5rem;
        }
        @media screen and (max-width:900px) {
            font-size: 2rem;
            padding-right: 0;
        }
        @media screen and (max-width:600px) {
            font-size: 1.5rem;
            padding-right: 0;
        }
        @media screen and (max-width:430px) {
            font-size: 1.3rem;
            padding-right: 0;
        }
    }

    &__flex{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        width:100%;
        gap:10rem;
        margin-bottom:5rem;
        @media screen and (max-width:900px) {
            gap:2rem;
        }

        &__left{
            width:65rem; 
            position:relative;
            border-radius: 2rem;
            @media screen and (max-width:1380px) {
                width:70rem;
            }

            @media screen and (max-width:1200px) {
                width:60rem;
            }
            @media screen and (max-width:630px) {
                width:90vw;
            }

            &__slider{
                overflow-y: scroll;
                display: flex;
                border-radius: 1rem;
                margin-bottom: 5rem;
                // width:65rem;
                &::-webkit-scrollbar{
                    display: none;
                }
    
                &>img{
                    width:65rem;
                    @media screen and (max-width:1380px) {
                        width:65rem;
                    }
                    @media screen and (max-width:1200px) {
                        width:65rem;
                    }
                    @media screen and (max-width:430px) {
                        object-fit: cover;
                        width:36rem;
                    }
                }
            }

            &__small-imgs{
                display: flex;
                gap:2rem;
                margin-bottom:5rem;
                @media screen and (max-width:660px) {
                    justify-content: center;
                }


                &>img{
                    width:17rem;
                    cursor: pointer;
                    border-radius: .7rem;
                    @media screen and (max-width:1200px) {
                        width:14rem;
                    }
                    @media screen and (max-width:660px) {
                        width:10rem;
                    }
                    @media screen and (max-width:630px) {
                        width:8rem;
                    }
                    @media screen and (max-width:430px) {
                        width:7rem;
                    }
                    
                }
            }

            &__car-details{
                padding:4rem 3rem;
                background-color: white;
                display: flex;
                flex-direction: column;
                gap:3rem;
                &>span{
                    font-size:2.5rem;
                    color:#1A3760;
                }

                &__columns{
                    display: flex;
                    justify-content: space-between;
                    font-size: 1.3rem;
                    color:gray;
                }
            }

        }

        &__right{
            display: flex;
            flex-direction: column;
            align-items: center;
            gap:1.5rem;
            border-radius: 1rem;
            padding:4rem 1rem;
            align-self: flex-start;
            opacity: 1;
            @media screen and (max-width:1040px) {
                width:90%;
            }
            @media screen and (max-width:600px) {
                width:100%;
            }
            &__btn-yellow,&__btn-blue{
                width:40rem;
                height:6rem;
                border:none;
                border-radius: 1rem;
                cursor: pointer;
                @media screen and (max-width:1380px) {
                    width:30rem;
                }
                @media screen and (max-width:1200px) {
                    width:25rem;
                }
            }

            &__btn-yellow{
                background-color:#F5C34B ;
                color:#1A3760;
                &:hover{
                    background-color: #cf9b21;
                }
            }
            &__btn-blue{
                color:white;
                background-color: #0d2a69;
                display: flex;
                align-items: center;
                justify-content:center;
                gap:2rem;
                font-size:1.5rem;
                font-weight: 200;
                &:hover{
                    background-color: #0b2356;
                }
            }


            &__contact{
                display: flex;
                flex-direction: column;
                align-items: center;
                background-color: white;
                border-radius: 1rem;
                width:100%;
                padding:3rem 2rem;
                gap:2rem;

                &__owner{
                    display: flex; 
                    align-items: center;
                    width:100%;
                    justify-content: space-between;

                    &>img{
                        width:6rem;
                        border-radius: 50%;
                        @media screen and (max-width:600px) {
                            width:4rem;
                        }
                    }
                    &>div{
                        display: flex;
                        flex-direction: column;
                        gap:1rem;
                        font-size:1.4rem;
                        align-items: flex-start;
                        @media screen and (max-width:600px) {
                            font-weight: 200;
                            font-size: 1.2rem;
                        }

                        &>span{
                            display: flex;
                            align-items: center;
                            gap:.5rem;
                        }
                    }
                }

                
                &__input{
                    width:100%;
                    height:5rem;
                    outline:none;
                    border:1px solid rgba(180, 179, 179, 0.621);
                    border-radius: .5rem;
                    padding-left:1rem;
                    &::placeholder{
                        font-weight: 200;
                    }
                }

                &__msg{
                    width:100%;
                    height:15rem;
                    outline:none;
                    border:1px solid rgba(180, 179, 179, 0.621);
                    border-radius: .5rem;
                    padding-left:1rem;
                    padding-top:1rem;
                    &::placeholder{
                        font-weight: 200;
                    }
                }

            }


        }
        
    }
}