.testDrivePopUp{
    width:100%;
    height:100vh;
    background-color: rgba(0, 0, 0, 0.3);
    position:fixed;
    top:0;
    left:0;
    display: flex;
    justify-content: center;
    align-items: center;

    &__exit{
        position: absolute;
        top: 0;
        left:0;
        width:100%;
        height:100%;
    }

    &__form{
        width:70rem;
        background-color: #0A2357;
        border-radius: 2rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap:3rem;
        padding-block: 5rem;
        z-index: 100;
        position:relative;
        @media screen and (max-width:650px) {
        border-radius: .5rem;
        width:65rem;
            
        }

        &__title{
            font-size: 1.7rem;
            color:white;
            letter-spacing: 1px;
        }

        &__input{
            width:25rem;
            height:4rem;
            border-radius: .5rem;
            padding-left: 1rem;
            outline: none;
            border: none;
            transition:.3s ease-in-out;
            &:focus{
                transform: translateY(-1rem) scale(1.1);
                box-shadow: 0 0 5px 1px #F5C34B;
            }

        }
        &__textarea{
            width:35rem;
            height:10rem;
            border-radius: .5rem;
            padding:1rem;
            outline: none;
            border: none;
            transition:.3s ease-in-out;
            &:focus{
                transform: translateY(-1rem) scale(1.1);
                box-shadow: 0 0 5px 1px #F5C34B;
            }
        }

        &__btn{
            width:18rem;
            height:4rem;
            cursor: pointer;
            background-color:#F5C34B ;
            border-radius: 1rem;
            cursor: pointer;
            outline: none;
            border: none;
            transition:.3s ease-in-out;
            &:hover{
                background-color:#a57505 ;
                color:white;
                transform: scale(1.05);
            }
        }
    }
}