.side-menu{
    position:fixed;
    top:0;
    width:70%;
    height:100vh;
    background-color: #0A2357;
    z-index: 2000;
    padding-block: 5rem;
    transition: .4s ease-in-out;

    
    &__logo{
        margin-left:3rem;
        &>img{
            width:12rem;
        }
    }

    &__list{
        position:absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        &>*{
            opacity:0;
        }

        &__item{
            color:white;
            margin:4rem 1rem;
            transition: .3s ease-out;
            transform: translateX(-100px);
            &:hover{
                transform: translateX(1rem);
            }
        }
    }
}